/* eslint-disable */
import React from 'react'
import '../css/detail.css'
import { Link, Outlet } from 'react-router-dom';
import Total from './Detail/Total'
import Explain from './Detail/Explain';
import Size from './Detail/Size';
import Steel from './Detail/Steel';

import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation } from 'swiper';

const tabs = [
    {
        id: 'tab1',
    },
    {
        id: 'tab2',
    },
    {
        id: 'tab3',
    }
]

export default function Detail() {
  return (
    <div className='detail_container'>
        <ul className='breadcrumb'>
            <li>
                <Link to='/'>
                    
                </Link>
            </li>
            <li></li>
            <li>
                <Link to='/sub'>
                    IKEA  
                </Link>
            </li>
            <li></li>
            <li>
                <Link to='/detail'>
                    GRILLSKR 
                </Link>
            </li>
        </ul>
        <div className="tab">
            <Swiper
                modules={[Navigation]}
                spaceBetween={20}
                slidesPerView={3}
                navigation
                loop={true}
                direction="vertical"
            >
                {
                tabs.map((visual, i) => {
                    return (
                        <SwiperSlide>
                            <img src={`/img/detail/tab${i+1}.png`} alt={`tab${i+1}`}/>
                        </SwiperSlide>
                    )
                })
                }
            </Swiper>
        </div>

        <div className="main_img">
            <img src="/img/detail/detail_main.png" alt="datail_main" />
        </div>
        <div class="sticky">
            <h2 class="sticky_title">GRILLSKR </h2>
            <p class="sticky_desc">, / </p>
            <p class="sticky_price"> 849,000</p>
            <p class="sticky_rating">
                 <span> 1,034</span>
            </p>
            <p class="sticky_notice">
                   .
            </p>
            <div class="sticky_info">
                <ul class="sticky_info_left">
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li> </li>
                    <li></li>
                </ul>
                <ul class="sticky_info_right">
                    <li></li>
                    <li>(China)</li>
                    <li>IKEA of Sweden AB</li>
                    <li>IKEA of Sweden</li>
                    <li>704.889.12</li>
                    <li>,  </li>
                </ul>
            </div>
            <div class="sticky_btn">
                <div class="sticky_cart"></div>
                <div class="sticky_buy"></div>
            </div>
        </div>
        <div className="info_container">
            <p className="main_desc">
                BBQ    !       ,<br/>             .
            </p>
            
            <ul className="info_tab">
                <li><Link to='/detail'>  </Link></li>
                <li><Link to='/detail/explain'> </Link></li>
                <li><Link to='/detail/size'></Link></li>
                <li><Link to='/detail/steel'> </Link></li>
            </ul>
            <Outlet>
                <Total/>
                <Explain/>
                <Size/>
                <Steel/>
            </Outlet>
        </div>
    </div>
  )
}
