/* eslint-disable */
import React from 'react'
import '../css/main.css'

import { Link } from 'react-router-dom';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, Autoplay } from 'swiper';

const visuals = [
    {id: 'visual1'},
    {id: 'visual2'},
    {id: 'visual3'},
    {id: 'visual4'}
]

const ideas = [
    {
        id: 'idea1',
        title: '  ',
        desc: ' ,           ',
        color: '#5d392b'
    },
    {
        id: 'idea2',
        title: ' ',
        desc: '    ,          .',
        color: '#606a69'
    },
    {
        id: 'idea3',
        title: '   ',
        desc: ' ,  ,  ...   .',
        color: '#fda389'
    }
]

const events = [
    {
        id: 'event1',
        title: ' ',
        subtitle: '     !',
        desc1: '   IKEA Family',
        desc2: '   !',
        color: '#a7d4bf'
    },
    {
        id: 'event1',
        title: '  ?',
        subtitle: ' 2  50%    !',
        desc1: ' IKEA   50   ',
        desc2: ' 50%  !',
        color: '#fee002'
    }
]



export default function Main() {
  return (
    <>
        <div className='visual_main'>
            <Swiper
                modules={[Navigation, Pagination, Autoplay]}
                spaceBetween={50}
                slidesPerView={1}
                navigation
                pagination={{ clickable: true }}
                autoplay={{ delay: 3000 }}
                loop={true}
            >
                {
                visuals.map((visual, i) => {
                    return (
                        <SwiperSlide>
                            <img src={`/img/main_visual${i+1}.png`} alt={`main_visual${i+1}`}/>
                        </SwiperSlide>
                    )
                })
                }
            </Swiper>
        </div>

    <div className='product'>
        <div className="product_img">
            <img src="/img/product.png" alt="" />
        </div>
        <div className="product_text">
            <div className="product_title">
                IKEA  
            </div>
            <div className="product_sub_title">
                  , IKEA
            </div>
            <div className="product_desc">
               ,<br/>     !
            </div>
            <Link to='/sub'>
                <div className="product_btn">
                      
                </div>
            </Link>
        </div>
    </div>

    <div className='event_container'>
    {
        events.map((event, i) => {
            return (
                <div className="event" key={event.id} style={{backgroundColor: event.color}}>
                    <div className="event_img">
                        <img src={`/img/event${i+1}.png`} alt={`event${i+1}`} />
                    </div>
                    <div className="event_text">
                        <div className="event_title">
                            {event.title}
                        </div>
                        <div className="event_sub_title">
                            {event.subtitle}
                        </div>
                        <div className="event_desc">
                            {event.desc1}
                        </div>
                        <div className="event_desc">
                            {event.desc2}
                        </div>
                    </div>
                </div>
            )
        })
    }

    </div>

    <div className='home_container'>
        <h3 className='home_title'> </h3>
        <div className="furnishing">
            <div className="furnishing_text">
                <div className="furnishing_title">
                      ,<br/>
                       
                </div>
                <div className="furnishing_desc">
                       ?<br/>
                        <br/>
                    ,     
                </div>
                <Link to='/sub'>
                    <div className="furnishing_btn">
                         
                    </div>
                </Link>
            </div>
            <div className="furnishing_img">
                <img src="/img/furnishing.png" alt="" />
            </div>
        </div>
        <h3 className="home_sub_title"> </h3>
        <div className="idea_container">
        {
            ideas.map((idea, i) => {
                return (
                    <div className="idea" key={idea.id} style={{backgroundColor: idea.color}}>
                        <div className="idea_img">
                            <img src={`/img/idea${i+1}.png`} alt={`idea${i+1}`}/>
                        </div>
                        <p className="idea_title">{idea.title}</p>
                        <p className="idea_desc">{idea.desc}</p>
                    </div>
                )
            })
        }
        </div>
    </div>

    </>
  )
}
